






















































	import graceMaskView from "../../graceUI/components/graceMaskView.vue";
export default {
	data() {
			return {
				staticUrl: this.staticUrl,
				show : false,
				nickname: uni.getStorageSync('login_key').nickname,
				avatar: uni.getStorageSync('login_key').avatar,
				userId: uni.getStorageSync('login_key').userId,
				password:uni.getStorageSync('login_key').password,
				changenickname:''
			};
		   },
		   
		    onShow() {
		   	var _this = this;
		   	uni.request({
		   		url: 'http://localhost:8080/api/user/' + uni.getStorageSync('login_key').userId,
		   		method: 'GET',
		   		header: { 'content-type': 'application/json' },
		   		success: res => {
		   			if (res.data.code === 0) {
		   				console.log(res.data.data.avatar+'————————————');
		   				_this.avatar = res.data.data.avatar;
		   				_this.nickname = res.data.data.nickname;
		   			}
		   		}
		   	});
		   },
		//下拉刷新事件
// 		onPullDownRefresh: function (){
// 			this.refreshFunc();
// 		}, 
		methods:{
			// 第1个演示 开启与关闭
			showBanner : function(){
				this.show = true;
			},
			closeBanner : function(){
				this.show = false;
			},
			rename:function(changenickname){
				if(this.changenickname.length==0){
					uni.showToast({
						title: '昵称不能为空！！',
						icon : "none"
					});
				}else{
				var _this = this;
				uni.request({
				url: 'http://localhost:8080/api/user/nickname?id='+uni.getStorageSync('login_key').userId,
				method:'put',
				data:changenickname,
				header: { 'content-type':'application/json' },
					success: res => {
						uni.showToast({
							title: '修改成功'
						});
						this.show=false;
						_this.nickname=this.changenickname;
						}
				});
				}
			},
			showActionSheet: function() {
				console.log('show');
				var _this = this;
				uni.showActionSheet({
					itemList: ['拍照', '从相册选择'],
					success: function(res) {
						console.log('选中了第' + (res.tapIndex + 1) + '个按钮');
						//选择的是拍照功能
						if (res.tapIndex == 0) {
							uni.chooseImage({
								count: 1,
								sourceType: ['camera'],
								success: function(res) {
									uni.saveImageToPhotosAlbum({
										filePath: res.tempFilePaths[0],
										success: function() {
											console.log('save success');
											uni.uploadFile({
												url: 'http://localhost:8080/api/user/avatar', //仅为示例，非真实的接口地址
												filePath: res.tempFilePaths[0],
												name: 'file',
												formData: {
													userId: _this.userId
												},
												success: uploadFileRes => {
													console.log(uploadFileRes.data);
													_this.avatar = uploadFileRes.data;
												}
											});
										}
									});
								}
							});
						}
						//从相册选择
						if (res.tapIndex == 1) {
							uni.chooseImage({
								count: 1, //默认9
								sizeType: ['original', 'compressed'], //可以指定是原图还是压缩图，默认二者都
								sourceType: ['album'], //从相册选择
								success: function(res) {
									console.log(JSON.stringify(res.tempFilePaths));
									uni.uploadFile({
										url: 'http://localhost:8080/api/user/avatar', 
										filePath: res.tempFilePaths[0],
										name: 'file',
										formData: {
											userId: _this.userId
										},
										success: uploadFileRes => {
											console.log(uploadFileRes.data);
											_this.avatar = uploadFileRes.data;
										}
									});
								}
							});
						}
					},
					fail: function(res) {
						console.log(res.errMsg);
					}
				});
			}
		},
		components :{
			graceMaskView
		}
}
